n\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 3, max: 40 }, availableCharacters: true, nonSpaceCharacter: true },\n    description: { minAndMaxLength: { min: 3, max: 100 }, availableCharacters: true, nonSpaceCharacter: true },\n});\n\nfunction addOrUpdateApplication() {\n    executor.execute(async () => {\n        if (props.app) {\n            executor.executeUseCase<IUpdateApplication>(IUpdateApplication, { ...props.app, name: name.value, description: description.value });\n        } else {\n            executor.executeUseCase<ICreateApplication>(ICreateApplication, { name: name.value!, description: description.value });\n        }\n        name.value = description.value = \"\";\n        emit(\"close\");\n    }, { loading: false });\n}\n\nfunction appNameValidationStatus(isValid: boolean) {\n    isAppNameValid.value = isValid\n\n}\n\nfunction descriptionValidationStatus(isValid: boolean) {\n    isDescriptionValid.value = isValid\n\n}\nconst addButtonDisabled = computed(() => !isAppNameValid.value || !isDescriptionValid.value);\n\n</script>\n\n<template>\n    <div class='updateOrCreateTextAreaDiv'>\n        <IDEInput :isRequired=\"true\" @validateStatus=\"appNameValidationStatus\" :placeholder=\"'Give your app a name'\"\n            :type=\"'text'\" info-text=\"Application Name\" :validations=\"validations.name\" />\n    </div>\n    <div class='updateOrCreateInputDiv'>\n        <IDEInput :isRequired=\"false\" @validateStatus=\"descriptionValidationStatus\" :placeholder=\"'Give your app a name'\" \n            :type=\"'textarea'\" info-text=\"Description\" :validations=\"validations.description\" />\n    </div>\n    <div class='updateOrCreateRow'>\n        <div class=\"col\">\n            <button :style=\"{ opacity: addButtonDisabled ? '0.5' : '' }\" class='updateOrCreateBtn'\n                @click=\"addOrUpdateApplication()\" :disabled=\"addButtonDisabled\">{{\n                        app ? \"UPDATE\" : \"CREATE\"\n                }}</button>\n        </div>\n    </div>\n\n\n\n\n</template>\n\n<style  scoped>\n.updateOrCreateTextAreaDiv {\n    margin-left: 28px;\n    margin-right: 28px;\n    margin-top: 30px;\n    height: 77px;\n}\n\n.appInfo {\n    color: #151233;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    opacity: 1;\n\n}\n\n\n\n.updateOrCreateTextarea {\n    width: 100%;\n    outline: none;\n    height: 120px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    resize: none;\n    margin-top: 3px;\n}\n\n.updateOrCreateInputDiv {\n    margin-top: 22px;\n    margin-left: 28px;\n    margin-right: 28px;\n    height: 173px;\n\n}\n\n.updateOrCreateInput {\n    width: 100%;\n    outline: none;\n    height: 40px;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    opacity: 1;\n    margin-top: 3px;\n}\n\n.updateOrCreateRow {\n    display: flex;\n    text-align: end;\n    margin-left: 28px;\n    margin-right: 28px;\n    border-top: 1px solid #E0E0E0;\n    margin-top: 32px;\n}\n\n.updateOrCreateBtn {\n    background-color: #151233;\n    color: #fff;\n    border-radius: 4px;\n    width: 79px;\n    height: 32px;\n    margin-top: 13px;\n    margin-bottom: 7px;\n    margin-top: 21px;\n    opacity: 1;\n    font-style: normal;\n    font-weight: 500;\n    font-size: 14px;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0.2px;\n    opacity: 1;\n}\n\n::placeholder {\n    padding-left: 10px;\n    padding-top: 5px;\n    color: #A1A1A1;\n    font-style: normal;\n    font-family: \"ROBOTO\";\n    letter-spacing: 0px;\n    font-size: 14px;\n    opacity: 1px;\n}\n</style>"],"sourceRoot":""}]);
